body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif, sans-serif;
  /*  'Franklin Gothic Medium', 'Arial Narrow',  */
  font-size: 16px;
  background-color: #f6f6f6;
  overflow-x: hidden;
}

input:focus {
  outline: 1px solid #4a4ae6;
}

/* colors */
.white {
  background-color: #fff;
}

/* cards w box shadow */
.general-card {
  -webkit-transition: -webkit-transform 500ms;
  transition: -webkit-transform 500ms;
  -o-transition: transform 500ms;
  transition: transform 500ms;
  transition: transform 500ms, -webkit-transform 500ms;
  padding: 20px;
  margin: 20px auto;
}

.shadow {
  box-shadow: 0px 0px 10px #b5b5b5;
  -webkit-box-shadow: 0px 0px 10px #b5b5b5;
  -moz-box-shadow: 0px 0px 10px #b5b5b5;
}

.deep-shadow-card {
  padding: 20px;
  -webkit-box-shadow: 0px 0px 20px #b5b5b5;
          box-shadow: 0px 0px 20px #b5b5b5;
}

/* general flex alignment classes */
.flex-center {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.flex-around {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

.flex-between {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.flex-center-column {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

/* buttons */
.button-main {
  padding: 12px 15px;
  color: white;
  border: none;
  background-color: #4a4ae6;
  font-size: 1.2em;
  margin: 10px;
  cursor: pointer;
}

/* links */
.link {
  color: #4a4ae6;
  padding-bottom: 2px;
  border-bottom: 1px solid #4a4ae6;
  cursor: pointer;
  margin: 10px 0;
}

.title {
  font-size: 1.5em;
  margin: 10px 0;
  text-align: center;
}



