.nav-container {
  height: 100%;
  width: 300px;
  -webkit-box-shadow: 0px 0px 10px grey;
          box-shadow: 0px 0px 10px grey;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  padding: 15px;
  background-color: #fff;
  position: absolute;
  top: 0;
}

.nav-logo-container {
  height: 60px;
  width: 60px;
  margin: 0 15px;
}

.nav-logo-container img {
  height: 100%;
  width: 100%;
}

.nav-link {
  color: #000;
  font-size: 1.1em;
  border-bottom: 1px solid transparent;
  padding: 5px;
  margin: 0 15px;
  -webkit-transition: padding 300ms;
  -o-transition: padding 300ms;
  transition: padding 300ms;
}

.nav-link:hover {
  border-bottom: 1px solid #000;
  cursor: pointer;
}

.history {
  width: 100%;
  padding: 10px 5px;
  border: 1px solid grey;
  margin: 10px auto;
}

.history-wrapper {
  width: 90%;
  margin: 0 auto;
}



